*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --font-special-elite: "Special Elite", system-ui;
    --font-chelsea-market: "Chelsea Market", system-ui;

    --font-size-xs: clamp(0.75rem, 0.6rem + 0.3vw, 0.9rem);
    --font-size-sm: clamp(0.9rem, 0.75rem + 0.4vw, 1.1rem);
    --font-size-md: clamp(1.1rem, 0.9rem + 0.5vw, 1.4rem);
    --font-size-lg: clamp(1.4rem, 1.1rem + 0.7vw, 1.8rem);
    --font-size-xl: clamp(1.8rem, 1.4rem + 1vw, 2.4rem);
    
    --color-grey-light: #c0c0c0;
    --color-grey-dark: #2d2d2d;
    --color-black: #1a1a1a;
    --color-white: #d1d1d1;
    --color-pink: #f04a92;
    --color-turqoise: #00f6f6;
    --color-shadow: #0006;

    --background-transparent: #0000;
    --background-purple: #b464ff32;
    --background-blue: #56aaffaa;
    --background-red: #64291e44;
    --background-lower: #080810;
    --background-middle: #0a0b12;
    --background-upper: #1a1c2c;
    --background-fog: #ffffff01;
}

html,
body {
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    font-family: var(--font-chelsea-market);
    background: 
    linear-gradient(
        91deg,
        var(--background-transparent) 58%,
        var(--background-purple) 70%,
        var(--background-transparent) 82%
    ), 
    linear-gradient(
        91deg,
        var(--background-transparent) 63%,
        var(--background-blue) 70%,
        var(--background-transparent) 77%
    ),
    radial-gradient(
        ellipse at 80% 50%,
        var(--background-red) 0%,
        var(--background-transparent) 50%
    ),
    radial-gradient(
        ellipse at 10% 10%,
        var(--background-red) 0%,
        var(--background-transparent) 50%
    ),
    linear-gradient(
        to top,
        var(--background-lower) 0%,
        var(--background-middle) 60%,
        var(--background-upper) 100%
    );
    background-blend-mode: screen, overlay, normal;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

main {
    flex: 1;
}

h1,
h2,
h3
 {
    font-family: var(--font-special-elite);
    color: var(--color-white);
    margin: 1em auto;
}

p,
a {
    color: var(--color-grey-light);
}

.container {
    max-width: 75ch;
    margin: 0 auto;
    padding: 1em;
}

.page-heading,
.header-text {
    font-size: var(--font-size-xl);
}

.section-heading {
    font-size: var(--font-size-lg);
    margin-bottom: 0.25em;
}

/* HEADER */

header {
    background-color: var(--color-black);
    border-bottom: 2px solid var(--color-pink);
}

nav {
    display: flex;
    gap: 1em;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.header-text {
    color: var(--color-turqoise);
    text-shadow: 2px 2px 2px var(--color-pink);
    margin: 0;
}

.btn {
    background-color: var(--color-grey-dark);
    border-color: var(--color-turqoise);
    color: var(--color-grey-light);
    font: inherit;
    font-size: var(--font-size-md);
    padding: 0.25em 1em;
}

.btn:hover,
.btn:active {
    box-shadow: 0px 0px 20px var(--color-pink);
}

/* HOME */

.home-page-text {
    font-size: var(--font-size-md);
    margin-bottom: 2em;
}

img {
    width: 100%;
    border: 2px solid var(--color-turqoise);
    box-shadow: 0px 0px 10px var(--color-pink);
    border-radius: 1em;
}

.img-container {
    position: relative;
}

.img-attrib {
    position: absolute;
    bottom: 0.5em;
    right: 0.5em;
    padding: 0.25em;
    color: var(--color-white);
    background-color: var(--color-shadow);
    border-radius: 0.5em;
    font-size: var(--font-size-sm);
}

/* MENU / ABOUT */

.section {
    border-top: 1px solid var(--color-pink);
    padding: 1em;
}

.section:last-child {
    border-bottom: 1px solid var(--color-pink);
}

.section-item {
    margin: 1em;
}

.section-text {
    font-size: var(--font-size-md);
    margin-bottom: 0.25em;
}

/* FOOTER */

footer {
    background-color: var(--color-black);
    border-top: 2px solid var(--color-pink);
    text-align: center;
}

.footer {
    font-size: var(--font-size-sm);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,WAAW;;AAKX;;;IAGI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,gDAAgD;IAChD,kDAAkD;;IAElD,sDAAsD;IACtD,sDAAsD;IACtD,qDAAqD;IACrD,qDAAqD;IACrD,mDAAmD;;IAEnD,2BAA2B;IAC3B,0BAA0B;IAC1B,sBAAsB;IACtB,sBAAsB;IACtB,qBAAqB;IACrB,yBAAyB;IACzB,qBAAqB;;IAErB,+BAA+B;IAC/B,8BAA8B;IAC9B,4BAA4B;IAC5B,2BAA2B;IAC3B,2BAA2B;IAC3B,4BAA4B;IAC5B,2BAA2B;IAC3B,2BAA2B;AAC/B;;AAEA;;IAEI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,eAAe;IACf,uCAAuC;IACvC;;;;;;;;;;;;;;;;;;;;;;;;;;;;KA4BC;IACD,8CAA8C;IAC9C,4BAA4B;IAC5B,sBAAsB;IACtB,4BAA4B;AAChC;;AAEA;IACI,OAAO;AACX;;AAEA;;;;IAII,sCAAsC;IACtC,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;;IAEI,8BAA8B;AAClC;;AAEA;IACI,eAAe;IACf,cAAc;IACd,YAAY;AAChB;;AAEA;;IAEI,8BAA8B;AAClC;;AAEA;IACI,8BAA8B;IAC9B,qBAAqB;AACzB;;AAEA,WAAW;;AAEX;IACI,oCAAoC;IACpC,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,4BAA4B;IAC5B,0CAA0C;IAC1C,SAAS;AACb;;AAEA;IACI,wCAAwC;IACxC,mCAAmC;IACnC,8BAA8B;IAC9B,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;;IAEI,0CAA0C;AAC9C;;AAEA,SAAS;;AAET;IACI,8BAA8B;IAC9B,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,uCAAuC;IACvC,0CAA0C;IAC1C,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,YAAY;IACZ,eAAe;IACf,yBAAyB;IACzB,qCAAqC;IACrC,oBAAoB;IACpB,8BAA8B;AAClC;;AAEA,iBAAiB;;AAEjB;IACI,uCAAuC;IACvC,YAAY;AAChB;;AAEA;IACI,0CAA0C;AAC9C;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,8BAA8B;IAC9B,qBAAqB;AACzB;;AAEA,WAAW;;AAEX;IACI,oCAAoC;IACpC,uCAAuC;IACvC,kBAAkB;AACtB;;AAEA;IACI,8BAA8B;AAClC","sourcesContent":["/* GLOBAL */\n\n@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Special+Elite&display=swap');\n\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\n:root {\n    --font-special-elite: \"Special Elite\", system-ui;\n    --font-chelsea-market: \"Chelsea Market\", 